header#header {
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 10;
	font-family: jf;
	font-size: 0.7em;
	height: 80px;
	background-color: rgba(255, 255, 255, 0.9);
	box-shadow: 0px 11px 10px -5px rgba(0, 0, 0, 0.3);
	line-height: 1.8em;

	div.elementscontainer {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;

		a {
			color: black;
			text-decoration: none;
			margin: 0 5px;
			display: inline-block;

			p {
				margin: 0;
			}
		}

		div.btn {
			display: inline-block;
			border-radius: 30px;
			margin: 0;
			padding: 0px 30px;
		}

		div.user {
			margin: 0 10px;
			padding: 0 10px;
			z-index: 5;
			width: 240px;
			height: 100%;
			background-color: #343746;
			display: flex;
			justify-content: space-between;
			align-items: center;
			display: none;

			div.image {
				width: 30%;
				height: 100%;
				display: flex;
				justify-content: center;
				align-items: center;

				div.userimage {
					background-size: 105% 105%;
					width: 55px;
					height: 55px;
					border-radius: 50%;
					border: 4px solid white;
					background-image: url("../images/global/user.svg");
				}
			}

			div.info {
				width: 70%;
				height: 100%;
				color: white;
				display: inline-flex;
				flex-direction: column;
				justify-content: center;

				p {
					line-height: 1.2em;
					word-wrap: break-word;
					text-align: center;

					a {
						color: #00aeef;
					}

					&:first-child {
						font-weight: bold;
					}
				}
			}
		}

		div.right {
			margin-right: 10px;
			height: 100%;
			position: relative;
			display: flex;
			align-items: center;

			div.btn {
				background-color: #cccccc;
				padding: 15px 25px;

				&.active {
					background-color: #ee145b;
					color: white;
				}
			}
		}

		div.left {
			height: 100%;
			background-color: rgba(255, 255, 255, 0);
			padding: 10px 0;

			a {
				display: inline-block;
				height: 100%;

				div.btn {
					display: flex;
					align-items: center;
					height: 100%;
					width: 100%;
					padding: 15px;
					background-color: #00aeef;

					p {
						color: white;
					}
				}

				&.register {
					div.btn {
						border-bottom-left-radius: 0;
						border-top-left-radius: 0;
					}
				}

				&.login {
					div.btn {
						// border-top-right-radius: 0;
						// border-bottom-right-radius: 0;

					}
				}
			}
		}

		div.logo {
			align-self: flex-start;
			width: 150px;
			height: 150px;
			background-color: #ed195b;
			padding: 0 5px 5px;
			border-bottom-right-radius: 50%;
			border-bottom-left-radius: 50%;

			div.inner {
				padding: 0 8px;
				border-bottom-right-radius: 50%;
				border-bottom-left-radius: 50%;
				width: 100%;
				height: 100%;
				border: 1px dashed white;
				border-top: 0px;

				img {
					width: 100%;
				}
			}
		}
		div.mobileright{
			display: none;
		}
		div.logo{
			display: block;
		}

		&.mobile{
			display: none;
		}
	}

	&.loggedin {
		div.elementscontainer {
			div.user {
				display: flex;
			}

			div.left {
				margin-left: 20px;

				a {
					&.register {
						display: none;
					}

					&.login {
						display: none;
					}

					&.logout {
						display: inline-block;
					}
				}
			}

			div.right {
				margin-right: 20px;
				margin-left: auto;
			}
		}
	}
}

@media only screen and (max-width: $tablet_media_query) {
	header#header {
		height: 60px;

		div.elementscontainer {
			div.user {
				width: 220px;
			}

			div.right {
				font-size: 0.8em;
				margin-right: 10px !important;

				a {
					div.btn {
						padding: 10px 20px;
					}
				}
			}

			div.left {
				padding: 8.5px 0;
				margin-right: auto;
				margin-left: 20px;

				a {
					div.btn {
						padding: 10px 20px;
					}
				}
			}

			div.logo {
				width: 100px;
				height: 100px;

				div.inner {
					padding: 0 5px;
				}
			}
		}
	}

	&.loggedin {
		div.elementscontainer {
			div.right {}

			div.left {
				margin: 0;
			}
		}
	}
}

@media only screen and (max-width: $mobile_media_query) {
	header#header {
		div.elementscontainer {
			div.mobileright {
				height: 100%;
				display: flex;
				align-items: center;
				padding: 10px;

				img {
					height: 40px;
				}
			}

			div.user{
				margin: 0;
			}

			div.logo {
				display: none;
			}

			div.right {
				display: none;
			}

			&.mobile {
				display: flex;
				background-color: rgba(255,255,255,0.9);
				&.show {
					height: 260px;
				}

				height: 0;
				transition: 0.5s height;
				overflow: hidden;
				flex-direction: column;

				a {
					height: 65px;
					margin: 0;
					width: 100%;
					display: inline-block;

					div {
						height: 100%;
						display: flex;
						justify-content: center;
						align-items: center;
						border-bottom: 1px solid #cccccc;

						p {
							font-size: 1.5em;
							text-align: center;
						}
					}

					&:last-child {
						div {
							border-bottom: none;
						}
					}
				}
			}
		}
	}
}