$tablet_media_query: 1023px;
$mobile_media_query: 767px;
body {
	margin: 0;
	font-size: 24px;
	overflow-x: hidden;
	overflow-y: hidden;
	padding: 0 !important;
	overflow-y: auto;
}

p {
	margin: 0;
}

* {
	box-sizing: border-box;
}

@font-face {
	font-family: isra;
	src: url(../fonts/isra.ttf);
}

@font-face {
	font-family: jf;
	src: url(../fonts/jf.ttf);
}

div.elementscontainer {
	min-width: 1024px;
	max-width: 1280px;
	margin: 0 auto;
	height: 100%;
}

::-webkit-input-placeholder {
	/* Chrome/Opera/Safari */
	font-size: 0.8em;
}

::-moz-placeholder {
	/* Firefox 19+ */
	font-size: 0.8em;
}

:-ms-input-placeholder {
	/* IE 10+ */
	font-size: 0.8em;
}

:-moz-placeholder {
	/* Firefox 18- */
	font-size: 0.8em;
}

/*=============================================
=            Tablets media query              =
=============================================*/
@media only screen and (max-width: $tablet_media_query) {
	div.elementscontainer {
		min-width: 768px;
		max-width: 1023px;
	}
}
/*=============================================
=            Mobiles media query              =
=============================================*/
@media only screen and (max-width: $mobile_media_query) {
	div.elementscontainer {
		min-width: 300px;
		max-width: 767px;
	}
}
