section {
	font-family: jf;
	padding-top: 80px;

	div.ads {
		div.elementscontainer {
			figure {
				width: 70%;
				height: 150px;
				margin: 20px 10px;
				display: flex;
				align-items: stretch;

				div.strip {
					width: 30px;
					background-color: #ed7e6a;
				}

				div.figure {
					width: 100%;
					background: linear-gradient(90deg, #e3e3e4 0%, #ffffff 100%);
				}
			}
		}
	}

	article {
		color: white;
		padding: 0 10px 40px;
		background-color: #f3f3f4;

		div.upper {
			width: 70%;
			padding-top: 30px;

			div.text {
				margin-bottom: 30px;
				height: 100px;
				background-color: white;
				border: 2px solid #bdbec0;
				border-radius: 20px;
			}

			div.line {
				border: 1px solid #cbcbcd;
				width: 95%;
				margin: 0 auto;
			}
		}

		div.lower {
			padding-top: 15px;
			display: flex;
			justify-content: space-around;

			div.mapsection {
				width: 70%;
				padding: 5px;
				background-color: #8cc640;
				border-radius: 20px;

				div.inner {
					padding: 5px;
					border: 2px dashed white;
					border-radius: 20px;

					h1 {
						font-size: 1.2em;
						margin: 10px;
						padding: 0;
						font-weight: normal;
						line-height: 1em;
					}

					div.mapfigure {
						width: 100%;
						height: 350px;
						position: relative;

						div.map {
							padding: 3px;
							width: 100%;
							height: 100%;
							position: absolute;
							background-color: #f3f3f4;
							border-radius: 3px;

							div#map {
								border-radius: 3px;
								color: black;
								width: 100%;
								height: 100%;
							}
						}

						div.btn {
							border-radius: 10px;
							top: -50px;
							position: absolute;
							padding: 5px;
							height: 150px;
							cursor: pointer;
							transition: 0.25s;
							font-size: 0.9em;
							width: 20%;

							div.inner {
								height: 100%;
								border-radius: 10px;
								display: flex;
								justify-content: center;
								align-items: flex-start;
							}

							&.one {
								background-color: #179dd9;
								left: 48%;
							}

							&.two {
								background-color: #fdc026;
								left: 25%;
							}

							&.three {
								background-color: #ed237c;
								left: 2%;
							}

							&.active {
								transform: translateY(-20%);
							}
						}
					}

					div.info {
						padding: 10px 20px;
						display: flex;
						justify-content: space-around;

						div.data {
							width: 65%;

							> div {
								h4 {
									border: 1px solid white;
									display: inline-flex;
									align-items: center;
									padding: 5px;
									font-weight: normal;
									font-size: 0.7em;
									border-radius: 30px;
									width: 140px;
									margin: 10px 0;

									span.arrow {
										font-size: 0.7em;
										width: 25px;
										display: inline-block;
										display: inline-flex;
										justify-content: center;
										align-items: flex-start;
									}
								}

								p {
									font-size: 0.6em;
									margin-right: 15px;
									border-bottom: 1px solid white;
									padding-bottom: 10px;
									margin-bottom: 10px;
								}

								&:last-child {
									p {
										border-bottom: 0;
										margin-bottom: 0;
										padding-bottom: 0;
									}
								}
							}
						}

						div.image {
							position: relative;
							width: 35%;

							img {
								width: 160%;
								position: absolute;
								right: -14%;
								bottom: -10%;
								pointer-events: none;
							}
						}
					}
				}
			}

			aside {
				width: 30%;
				color: #5d5e5e;
				padding: 0 20px;

				h1 {
					margin: 5px;
					font-weight: normal;
				}

				form {
					width: 100%;

					div.contactform {
						width: 100%;
						background-color: #e7e8e9;
						padding: 4px;
						border-radius: 10px;

						div.inner {
							width: 100%;
							padding: 10px;
							border: 2px dashed white;
							border-radius: 10px;

							div.inputrow {
								display: flex;
								flex-direction: column;
								margin: 15px 0;

								label {
									font-size: 0.7em;
									display: block;
									margin: 5px 0;
								}

								input,
								textarea {
									width: 100%;
									font-family: jf;
									border: 1px solid #d1d2d4;
									background-color: #ffffff;
									border-radius: 5px;
									padding: 2px 10px;
									font-size: 0.7em;
									outline: none;
								}

								input {
									height: 35px;

									&:focus {
										border-radius: 5px;
										border: 1px solid rgba(81, 203, 238, 1);
									}
								}

								textarea {
									resize: none;
								}

								&:first-child {
									margin-top: 0;
								}
							}
						}
					}

					div.submitform {
						margin-top: 10px;
						display: flex;
						justify-content: space-around;
						align-items: center;

						input[type=submit], button {
							cursor: pointer;
							font-family: jf;
							font-size: 0.7em;
							width: 100px;
							height: 50px;
							color: white;
							background-image: url("../images/contactpage/btn.svg");
							background-color: rgba(0, 0, 0, 0);
							border: none;
							background-repeat: no-repeat;
							background-size: cover;

							&:active {
								background-image: url("../images/contactpage/btndown.svg");
							}
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section {
		padding-top: 60px;

		div.ads {
			div.elementscontainer {
				figure {
					width: 95%;
					margin-left: auto;
					margin-right: auto;
				}
			}
		}

		article {
			padding: 0;

			div.elementscontainer {
				div.upper {
					width: 95%;
					margin: 0 auto;
				}

				div.lower {
					flex-direction: column;
					align-items: center;

					div.mapsection {
						width: 95%;

						div.inner {
							div.info {
								div.image {
									display: none;
								}

								div.data {
									> div {
										text-align: center;
									}
								}
							}
						}
					}

					aside {
						margin: 20px 0;
						width: 95%;
						padding: 0;

						h1 {
							text-align: center;
						}

						form {
							div.contactform {
								div.inner {
									div.inputrow {
										flex-direction: row;

										label {
											width: 30%;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Mobile media query till 320px    =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	section {
		div.ads {
			div.elementscontainer {
				figure {}
			}
		}

		article {
			div.elementscontainer {
				div.upper {}

				div.lower {
					div.mapsection {
						div.inner {
							h1{
								font-size: 4vw;
							}
							div.info {
								div.image {}

								div.data {
									width: 100%;

									> div {}
								}
							}
							div.mapfigure{
								div.btn{
									font-size: 3vw;
								}
							}
						}
					}

					aside {
						h1 {}

						form {
							div.contactform {
								div.inner {
									div.inputrow {
										label {
											width: 45%;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}